import testBSC from './test/BSC'
import testMOON from './test/MOON'


export const ChainIdMap = {
    test_MOON: 1287,
    test_BSC: 97
}

const switchNet = (chainId)=>{
    if (chainId === ChainIdMap.test_BSC) {
        window.mainAddress = testBSC
        window.serverApi = 'https://test-backend.subdao.org/chapel/query'
        window.graphApi = 'https://api.thegraph.com/subgraphs/name/xiaosongfu/subdao-subgraph-chapel'
    } else if (chainId === ChainIdMap.test_MOON) {
        window.mainAddress = testMOON
        window.serverApi = 'https://test-backend.subdao.org/api/query'
        window.graphApi = 'https://api.thegraph.com/subgraphs/name/xiaosongfu/subdao-subgraph'
    }
}

export default switchNet